import { BadRequestException, Injectable, Logger, UnauthorizedException } from '@nestjs/common';
import { JwtService } from '@nestjs/jwt';
import * as bcrypt from 'bcryptjs';
import { PrismaService } from '../../infra/prisma/prisma.service';

@Injectable()
export class AuthService {
  private readonly logger = new Logger('AuthService');

  constructor(
    private readonly prisma: PrismaService,
    private readonly jwt: JwtService,
  ) {}

  private async hashPassword(plain: string) {
    const salt = await bcrypt.genSalt(10);
    return bcrypt.hash(plain, salt);
  }

  private async verifyPassword(plain: string, hash: string) {
    return bcrypt.compare(plain, hash);
  }

  async signup(email: string, password: string) {
    if (!email || !password) throw new BadRequestException('email and password required');
    const hashed = await this.hashPassword(password);

    const user = await this.prisma.user.upsert({
      where: { email },
      update: { password: hashed },
      create: { email, password: hashed },
      select: { id: true, email: true },
    });

    const access_token = await this.jwt.signAsync({ sub: user.id, email: user.email });
    return { access_token };
  }

  async login(email: string, password: string) {
    try {
      if (!email || !password) throw new BadRequestException('email and password required');

      const user = await this.prisma.user.findUnique({
        where: { email },
        select: { id: true, email: true, password: true },
      });

      this.logger.log(`login attempt email=${email} found=${!!user}`);
      if (!user) throw new UnauthorizedException('Invalid email or password');

      const ok = await this.verifyPassword(password, user.password);
      this.logger.log(`bcrypt compare ok=${ok} hash_head=${user.password?.slice(0,4)}`);
      if (!ok) throw new UnauthorizedException('Invalid email or password');

      await this.prisma.user.update({
        where: { id: user.id },
        data: { lastLoginAt: new Date() },
      });

      const access_token = await this.jwt.signAsync({ sub: user.id, email: user.email });
      return { access_token };
    } catch (err) {
      // Normalize unexpected errors to a safe Unauthorized to avoid 500s
      if (!(err instanceof BadRequestException || err instanceof UnauthorizedException)) {
        this.logger.error(`login failed for ${email}: ${err instanceof Error ? err.message : String(err)}`);
        throw new UnauthorizedException('Invalid email or password');
      }
      throw err;
    }
  }
}